.features {
  background: #3a454d;
  padding: 80px 0;
  .title {
    h3 {
      color: #fff !important;
    }
  }
  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .item {
      max-width: 360px;
      margin: 30px 15px;
      .lg(text-align,center);
      img {
        margin-bottom: 50px;
      }
      h5 {
        margin-bottom: 30px;
        font-size: 18px;
        font-family: 'Quattrocento Sans', sans-serif;
        color: rgb(255, 255, 255);
        font-weight: bold;
        text-transform: uppercase;
      }
      p {
        font-size: 16px;
        font-family: 'Quattrocento Sans', sans-serif;
        color: rgb(220, 223, 225);
        line-height: 1.625;
        max-width: 344px;
      }
    }
  }
}