.work {
  padding-top: 80px;
  padding-bottom: 60px;
  background: #3a454d;
  .item {
    position: relative;
    margin: 15px 0;
    &:hover{
  
      i{
        opacity: .8;
        transition: opacity 2.5s;
      }
      .bg{
        opacity: .9;
        transition: opacity 2s;
      }
      .text{
        opacity: 8;
        transition: opacity 2s;
        width: 100%;
      }
    }
    img{
      width: 100%;
    }
    i {
      position: absolute;
      top: 35%;
      text-align: center;
      width: 100%;
      color: #fff;
      opacity: 0;
    }
    .bg {
      background: url('../img/workitembg.png');
      width: 100%;
      height: 80%;
      position: absolute;
      top: 0;
      opacity: 0;
    }
    .text {
      width: 10%;
      opacity: 0;
      background: #292f36;
      padding: 20px 0;
      padding-left: 20px;
      position: absolute;
      bottom: 0;
 
      p {
        .media-block({
          font-size: 16px;
        },350px);
        font-size: 18px;
        font-family: 'Quattrocento Sans', sans-serif;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        margin: 0;
      }
    }
  }
}