.service {
  padding-top: 80px;
  padding-bottom: 100px;
  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .item {
      max-width: 250px;
      margin: 45px 15px;
      img {
        display: block;
        margin: auto;
        margin-bottom: 30px;
      }
      h4 {
        text-align: center;
        font-size: 17px;
        font-family: 'Quattrocento Sans', sans-serif;
        color: rgb(41, 47, 54);
        font-weight: bold;
        text-transform: uppercase;
        margin-bottom: 30px;
      }
      p {
        margin: 0;
        font-size: 16px;
        font-family: 'Open Sans', sans-serif;
        color: rgb(141, 143, 146);
        line-height: 1.625;
        text-align: center;
        max-width: 255px;
      }
    }
  }
}