.team {
  padding: 50px 0;
  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .item {
      margin: 15px;
      max-width: 264px;
      &:hover {
        .item-top {
          .bg {
            transition: opacity 2s;
            opacity: 1;
          }
          .icon {
            opacity: 1;
            transition: opacity 2s;
            
          }
        }
        .text {
          background: #3a454d;
          transition: background 2s;
        }
      }
      .item-top {
        position: relative;
        img {}
        .bg {
          background: url('../img/itembg.png');
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          opacity: 0;
          
        }
        .icon {
          opacity: 0;
          display: block;
          line-height: 40px;
          text-align: center;
          position: absolute;
          top: 45%;
          left: 20px;
          margin-left: -18.5px;
          margin-top: -20px;
          width: 100%;
          a {
            text-decoration: none;
            padding: 14px 18px;
            background: #3a454d;
            background: url('../img/iconbg.png');
            margin: 1px;
            i {
              color: #fff;
              font-size: 18px;
            }
          }
        }
      }
      .text {
        background: url('../img/itembg.png');
        padding: 20px 0;
        padding-left: 20px;
        h3 {
          font-size: 16px;
          font-family: "Myriad Pro";
          color: rgb(41, 47, 54);
          text-transform: uppercase;
          text-align: left;
        }
        i {
          font-size: 14px;
          font-family: "Myriad Pro";
          color: rgb(141, 143, 146);
        }
      }
    }
  }
}