.modest {
  padding-top: 80px;
  padding-bottom: 80px;
  .mainModest {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .modestText {
      width: 55%;
      .lg(width, 100%);
      .lg(text-align, center);
      p {
        font-size: 16px;
        color: rgb(141, 143, 146);
        line-height: 1.625;
        font-family: 'Quattrocento Sans', sans-serif;
        margin: 19px 0;
        &:nth-child(1) {
          margin-top: 0;
        }
      }
      .icons {
        margin-top: 80px;
        display: flex;
        margin-bottom: 40px;
        .lg(justify-content, center);
        a {
          text-decoration: none;
          text-align: center;
          line-height: 4;
          width: 60px;
          height: 60px;
          background:#c8cdd0;
          margin: 1px;
          &:hover{
            background: #d0d5d9;
            opacity: .8;
           
          }
          i {
            color: #fff;
            font-size: 18px;
          }
        }
      }
    }
    .dedication {
      width: 40%;
      .lg(width,
      100%);
      .lg(margin,
      auto);
      ul {
        padding: 0;
        li {
          display: flex;
          list-style: none;
          .lg-block( {
            flex-direction: column;
            text-align: center;
          }
          );
          .number {
            .lg-block( {
              margin: 15px auto;
            }
            );
            text-align: center;
            line-height: 3;
            padding: 1px 16px;
            align-self: flex-start;
            background: #23b7a4;
            color: #fff;
          }
          .text {
            margin-left: 20px;
            h4 {
              font-size: 18px;
              font-family: 'Open Sans', sans-serif;
              color: rgb(41, 47, 54);
              text-transform: uppercase;
              font-weight: 700;
            }
            p {
              font-size: 16px;
              font-family: 'Open Sans', sans-serif;
              color: rgb(141, 143, 146);
              line-height: 1.625;
            }
          }
        }
      }
    }
  }
}