header {
  position: relative;
  .menu-top {
    background: #1d2126;
    .navbar {
      .navbar-nav {
        li {
          margin: 0 15px;
          .lg(align-self,center);
          &.active {
            border: #23b7a4 1px solid;
            border-radius: 5px;
            padding: 0;
            a {
              color: #23b7a4;
            }
          }
          a {
            padding: 8px 10px;
            font-size: 14px;
            font-family: 'Open Sans', sans-serif;
            color: rgb(164, 174, 173);
            text-transform: uppercase;
          }
        }
      }
    }
  }
  .carousel {
    .carousel-indicators {
      justify-content: end;
      li {
        &.active {
          background: #fff;
        }
        width: 8px;
        height: 8px;
        background: transparent;
        border: 1px solid #fff;
        border-radius: 50%;
      }
    }
    .carousel-inner {
      .carousel-item {
        img {
          object-fit: cover;
        }
        .carousel-caption {
          top: 25%;
          .media-block({
            top: 0;
          },874px);
          h3 {
            font-size: 36px;
            font-family: 'Quattrocento Sans', sans-serif;
            color: rgb(255, 255, 255);
            text-transform: uppercase;
            text-align: left;
          }
          p {
            text-align: left;
            font-size: 16px;
            font-family: 'Quattrocento Sans', sans-serif;
            color: rgb(204, 208, 215);
            max-width: 570px;
          }
          .button-group {
            text-align: left;
            input {
              border: 1px solid #fff;
              border-radius: 5px;
              color: #fff;
              background: transparent;
              font-size: 14px;
              font-family: 'Quattrocento Sans', sans-serif;
              font-weight: bold;
              text-transform: uppercase;
              line-height: 2;
              padding: 6px 9px;
              &:hover {
                background: #23b7a4;
                border-color: #23b7a4;
              }
            }
          }
        }
      }
    }
  }
}