.contact {
  padding: 40px 0;
  h4 {
    margin-bottom: 30px;
    font-size: 20px;
    font-family: 'Quattrocento Sans', sans-serif;
    color: rgb(41, 47, 54);
    text-transform: uppercase;
    line-height: 1.3;
    text-align: left;
    .lg(text-align,center);
  }
  p {
    .lg(text-align, center);
    font-size: 16px;
    font-family: 'Quattrocento Sans', sans-serif;
    color: rgb(141, 143, 146);
    line-height: 1.625;
    width: 100%;
    &.contactText{
        margin-bottom: 40px;
    }
   
  }
  .formi {
    transform: translateY(-46%);
    background: #fff;
    padding: 30px;
    .lg-block( {
      transform: translateY(0%);
    }
    );
    form {
      label {
        font-size: 14px;
        font-family: 'Quattrocento Sans', sans-serif;
        color: rgb(141, 143, 146);
        font-weight: bold;
        text-transform: uppercase;
      }
      input {
        border: none;
        background: url('../img/inputbg.png');
      }
      textarea {
        border: none;
        background: url('../img/inputbg.png');
        margin-bottom: 30px;
        height: 160px;
      }
      button {
        border-style: solid;
        border-width: 2px;
        border-color: rgb(35, 183, 164);
        border-radius: 3px;
        background-color: rgb(35, 183, 164);
        width: 151px;
        height: 44px;
        font-size: 14px;
        font-family: 'Quattrocento Sans', sans-serif;
        color: rgb(255, 255, 255);
        font-weight: bold;
        text-transform: uppercase;
        line-height: 2;
      }
    }
  }
}